import React from "react";

export default function ToggleButton(props) {
  return (
    <button
      style={{ float: "right" }}
      onClick={() => {
        props.setLang(!props.lang);
      }}
      type="button"
      className="log-out-button"
    >
      {props.lang ? <>EN</> : <>TR</>}
    </button>
  );
}
